body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-navbar{
    background: #f0f0f0;
    border-bottom: 1px solid #ccc;
}

.navbar-list{
    display: inline-block;
    list-style: none;    
    border-left: 1px solid #ccc;
    letter-spacing: -1px;
}
.navbar-list:last-of-type{
    border-right: 1px solid #ccc;
}

.navbar-list:hover{
    background: #262626;
}

.navbar-orderlist{
    padding: 0;
    margin-bottom: 0;

}

.navbar-list-item{
    text-decoration: none!important;
    font-family: "Trebuchet MS";
    display: block;
    width: 190px;
    font-size: 21px;
    padding: 18px 0;
    color: #262626;
    text-align: center; 
}

.navbar-list-item:hover{
    color: #fff;
}

.navbar-list-active a{
    background: #262626;
    color: #fff;
}


@media screen and (max-width: 1200px){
    .navbar-list-item{
        width: 160px;
        font-size: 20px;
        padding: 15px 0;
    }    
}

@media screen and (max-width: 992px){
    .navbar-list-item{
        width: 135px;
        font-size: 20px;
        padding: 10px 0;
    }   
}

@media screen and (max-width: 768px){
    .nav-navbar{
        border-bottom: none;
        width: 275px;
        height: 100vh;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1000;
    }

    .navbar-show {
        display: block;
    }

    .navbar-hide {
        display: none;
    }

    
    .nav-navbar .container{
        padding-left: 0;
        padding-right: 0;
    }

    .navbar-list{
        display:block;
        border-left: none;
        letter-spacing: -1px;
    }
    .navbar-list:last-of-type{
        border-right: none;
    }
    
    .navbar-list-item{
        width: 100%;
        font-size: 18px;
    }
            
}
.header-main{
    font-family: "Trebuchet MS";
    background : url(data:image/gif;base64,R0lGODlhAQBlALMAAPv7+/j4+Pz8/Pn5+fb29vPz8/r6+vf39/X19f39/f7+/vT09P///wAAAAAAAAAAACH5BAAAAAAALAAAAAABAGUAAAQjkJVZlr0oZ8LJ+UcgBkM5GKgBrIDgCkkcK3TN3Hiu73zvMxEAOw==) left top repeat-x #fff;
    z-index: 2;
    height: 100px;
    box-shadow: 0 5px 15px -5px #000;
}


.header-content{
    margin-left: auto;
    margin-right: auto;
    width: 960px; 
    height: 100px;   
}

.header-content h1{
    padding: 15px 0 0 0;
}

.header-content h1 span:nth-child(1){
    color: #ff9601;
}

.header-content h1 span:nth-child(2){
    color: #949494;
    font-size: 28px;
    letter-spacing: -1px;
    vertical-align: center;
}

.site-logo{
    display: inline-block;
    font-size: 60px;
    line-height: 1.2em;
    color: #262626;
    letter-spacing: -3px;
}

.site-logo a{
    text-decoration: none!important;
    color: #262626;
}

.site-logo a:hover{
    color: #262626;
}

.left-content{
    display: inline-block;
    float: left;
}

.right-content{
    float: right;
    display: inline-block;
    margin: 35px 0;
}


.right-content input{
    outline: none;
    box-sizing: border-box!important;
    width: 200px;
    padding: 2px 25px 2px 10px;
    background: url(data:image/gif;base64,) no-repeat 98% 50%;
    border: 1px solid #ccc;
    z-index: -10;
}

.menu-overlay {
    background: rgba(0,0,0,0.5);
    position: fixed;
    top: 60px;
    left: 275px;
    height: 100%;
    width: 100%;
    z-index: 100;
}

.menu-overlay-show{
    display: block;
}


.menu-overlay-hide{
    display: none;
}

.menu-bar{
    display: none;
    position: absolute;
    top: 15px;
    right: 5%;
}

.fa-bars{
    font-size: 32px;
    color: #ff9601;
}

.fa-times{
    font-size: 32px;
    color: #ff9601;
}

@media screen and (max-width: 1200px){
    .header-main{
        height: 80px!important;
    }
    .header-content{
        margin-left: auto;
        margin-right: auto;
        width: 810px;
        height: 80px!important;    
    }

    .header-content h1{
        padding: 5px 0 0 0;
    }

    
    .header-content h1 span:nth-child(2){
        font-size: 24px;
    }

    .site-logo{        
        font-size: 50px;
        line-height: 1.4em;
    }

    .right-content{
        margin: 25px 0;
    }
}

@media screen and (max-width: 992px){
    .header-main{
        height: 60px!important;
    }

    .header-content{
        height: 60px!important;
        width: 685px;    
    }    

    .header-content h1{
        padding: 0;
    }

    
    .header-content h1 span:nth-child(2){
        font-size: 20px;
    }

    .site-logo{        
        font-size: 42px;
        line-height: auto;
    }

    .right-content{
        margin: 15px 0;
    }   

}

@media screen and (max-width: 768px) {
    .header-main .container{
        margin: 0 5%;
    }

    .right-content{
        margin-right: 18%;
    }
    
    
    .right-content input{
        outline: none;
        box-sizing: border-box!important;
        width: 200px;
        padding: 2px 25px 2px 10px;
        background: url(data:image/gif;base64,) no-repeat 98% 50%;
        border: 1px solid #ccc;
        z-index: -10;
    }
    
    .menu-bar{
        display: block;
        position: absolute;
        top: 15px;
        right: 5%;
    }

    .fa-bars{
        font-size: 32px;
        color: #ff9601;
    }
    
    .fa-times{
        font-size: 32px;
        color: #ff9601;
    }
    
    
}

@media screen and (max-width: 575px){   
    .header-content{
        margin-left:5%;
    }
    .header-main .container{
        margin: 0;
    }

    .site-logo{
        font-size: 32px;
        line-height: 1.9em;
    }

    .right-content{
        margin-left: 5%;
        margin-right: 20%;
    }
    
    .right-content input{
        width: 150px;
    }

    .menu-bar{
        position: absolute;
        top: 15px;
        right: 5%;
    }

}


@media screen and (max-width: 480px){   
    .right-content{
        margin-left: 5%;
        margin-right: 17.5%;
    }
    
    .right-content input{
        width: 60px;
    }
}
.Banner {
    background: url(/interiordesign/static/media/bg-top.da6c28ee.jpg) center top;
    height: auto;
    background-size: cover;
    overflow-x: hidden;
}
.banner-content{
    padding: 40px 0px;
    margin-left: auto;
    margin-right: auto;
    width: 980px;
    height: auto;
}
.banner-image-gallery{
    background: #fff;
    padding: 20px 8px 20px 20px;
}

.right-banner-image-gallery img{
    padding: 0 22px 20px 0;
    position: relative;
}

.gallery-img{
    cursor: pointer;
}

.img-overlay-set{
    background: rgba(0,0,0,0.5);
    width: 140px;
    height: 120px;
    z-index: 200;
    position: absolute;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.img-overlay-set:hover{
    background: none;
}

.img-overlay-unset{
    background: none;
}

.banner-quote h2{
    color: #ff9601;
    font-size: 48px;
    margin: 0 10px;
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    letter-spacing: -3px;
    padding: 5px 25px 0 0;
}

.banner-quote p{
    margin: 0 10px;
    font-size: 14px;
}

.banner-quote p a{
    color: #ff9601;
    text-decoration: underline;
}

.banner-quote p a:hover{
    text-decoration: none;
}

.service-content {
    padding: 20px 10px 0 10px;
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
}

.service-content h2{
    font-size: 36px;
    color: #fff;
    margin: 0 10px 15px 10px;
    font-weight: normal;
    letter-spacing: -2px;
}

.service-content img{
    background: #fff;
    padding: 10px;
    display: inline;
}

.service-content img{
    float: left;
}

.service-content ul{
    overflow: hidden;
}

.service-content ul li{
    color: #ff9601;
    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAcAAAAGCAYAAAAPDoR2AAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAADhJREFUeNpiYACC////GwPx3f8QAKJdGKASSkD87j8qAPGVQJLl/7GDciYGfACvsTgcZAwSBwgwALXIcxji1M9OAAAAAElFTkSuQmCC) no-repeat 0 16px;
    font-size: 20px;
    list-style: none;
    display: inline-block;
    letter-spacing: -1px;
    padding: 2px 15px;
}

@media screen and (max-width: 1200px){
    .banner-content{
        width: 100%;
    }
    .left-banner-image-gallery img{
        width: 100%;
    }
    .right-banner-image-gallery{
        margin-left: 5px;
    }
    
    .right-banner-image-gallery img{
        padding: 0 22px 20px 0;        
        position: relative;
        width: 150px;
        height: 135px;
    }
    
    .img-overlay-set{
        width: 128px;
        height: 115px;
        z-index: 200;
    }
    .service-content ul{
        padding-left: 20px;
    }   
}

@media screen and (max-width: 992px){
    .Banner{
        background: -webkit-linear-gradient(
        rgba(0,0,0,0.3),
        rgba(0,0,0,0.5)
        ),
    url(/interiordesign/static/media/bg-top.da6c28ee.jpg) center top;
        background: linear-gradient(
        rgba(0,0,0,0.3),
        rgba(0,0,0,0.5)
        ),
    url(/interiordesign/static/media/bg-top.da6c28ee.jpg) center top;
    }

    .banner-image-gallery{
        padding: 20px;
    }
    .right-banner-image-gallery{
        margin: 20px 4%;
    }
    .right-banner-image-gallery img{
        width: 300px;
        height: 200px;
        padding: 0;
        margin: 10px 7px 5px 7px;
    }
    .img-overlay-set{
        width: 300px;
        height: 200px;
        margin: 10px 0 0 7px;
    }

    .banner-quote h2{
        font-size: 42px;
        text-align: center;
        padding: 0;
    }
    
    .banner-quote p{
        margin: 10px;
        font-size: 16px;
        text-align: center;
    }
    .service-content {
        text-align: center!important;
    }
    
    .service-content h2{
        font-size: 32px;
        padding: 20px 5px;
    }
    
    .service-content img{
        width: 50%;
        margin: 10px;
    }
    
    
    .service-content ul{
        margin-top: 25px;
        text-align: left;
    }
    
    .service-content ul li{
        font-size: 22px;
    }

}

@media screen and (max-width: 768px){
    .Banner .container{
        margin-left: auto;
        margin-right: auto;
    }

    .banner-image-gallery{
        padding: 15px;
    }
    .right-banner-image-gallery{
        margin: 9px;
    }
    .right-banner-image-gallery img{
        width: auto;
        padding: 0;
        margin: 10px 7.5px 5px 7px;
    }
    .img-overlay-set{
        width: 230px;
        margin-left:9px;
    }
    
    .banner-quote p{
        font-size: 18px;
    }
    
    .service-content h2{        
        padding: 10px 5px;
    }
    
    .service-content img{
        width: auto;
    }
    
    
    .service-content ul{
        width: auto;
    }
    
    .service-content ul li{
        font-size: 18px;
    }
}

@media screen and (max-width: 575px){
    .right-banner-image-gallery{
        margin: 0 auto;
        width: 400px;
        
    }
    .right-banner-image-gallery img{
        width: 200px;
        height: 120px;
        padding: 0;
    }
    .img-overlay-set{
        width: 200px;
        margin-left: 8px;
        height: 120px;
    }
    .banner-quote h2{
        margin: 15px 10px 0px 10px;
    }

    .service-content img{
        width: 100%;
        margin: auto;
    }

    .service-content ul{
        text-align: center;
    }
    
    .service-content ul li{
        background-position: 0px 10px;
        display: block;
        font-size: 16px;
    }
}

@media screen and (max-width: 475px){
    .right-banner-image-gallery{
        margin: 0 auto;
        width: 300px;
        
    }
    .right-banner-image-gallery img{
        width: 150px;        
    }
    .img-overlay-set{
        width: 150px;
    }
}

@media screen and (max-width: 375px){
    .right-banner-image-gallery{
        margin: 0 auto;
        width: 260px;
    }
    .right-banner-image-gallery img{
        width: 130px;        
    }
    .img-overlay-set{
        width: 130px;
    }
}
.Footer{
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
}

